import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
    tasks: [],
    status: 'idle',
    self_link: "/api/tasks",
    error: null
}

export const selectAllTasks = state => state.tasks.tasks;
export const selectTaskById = (state, taskId) => state.tasks.tasks.find(task => task.id === taskId);

export const fetchTasks = createAsyncThunk('tasks/fetchTasks', async () => {
    const response = await axios.get(`api/tasks`);
    return {
        tasks: response.data._embedded.tasks,
        self_link: response.data._links.self.href
    }
  });

export const addNewTask = createAsyncThunk(
    'tasks/addNewTask',
    async (initialTask) => {
        const response = await axios.post('/api/tasks', initialTask);
        return response.data;
    }
)

export const updateTaskState = createAsyncThunk(
    'tasks/updateTaskState',
    async existingTask => {
        let newState = "";
            if (existingTask) {
                if(existingTask.state === "TODO") {
                    newState = "IN_PROGRESS";
                } else if (existingTask.state === "IN_PROGRESS") {
                    newState = "DONE";
                } else if (existingTask.state === "DONE"){
                    newState = "ARCHIVED";
                }
            }

            const response = await axios.patch(existingTask._links.self.href, {
                state: newState
            });
            return response.data;
    }
)

export const removeTask = createAsyncThunk(
    'tasks/deleteTask',
    async task => {
        const response = await axios.delete(task._links.self.href);
        return task;
    }
)

export const tasksSlice = createSlice({
    name: 'tasks',
    initialState,
    reducers: {
    },
    extraReducers(builder) {
        builder
          .addCase(fetchTasks.pending, (state, action) => {
            state.status = 'loading'
          })
          .addCase(fetchTasks.fulfilled, (state, action) => {
              console.log("fetch fullfilled");
            state.status = 'succeeded';
            // Add any fetched posts to the array
            state.tasks = action.payload.tasks;
            state.self_link = action.payload.self_link;
          })
          .addCase(fetchTasks.rejected, (state, action) => {
            state.status = 'failed'
            state.error = action.error.message
          })
          .addCase(addNewTask.fulfilled, (state, action) => {
            state.tasks.push(action.payload);
          })
          .addCase(removeTask.fulfilled, (state, action) =>{
              console.log(action);
              state.tasks = state.tasks.filter(t => t.name !== action.payload.name);
          })
          .addCase(updateTaskState.fulfilled, (state, action) => {
              state.tasks = state.tasks.filter(t => t.name !== action.payload.name);
              state.tasks.push(action.payload);
          });
      }
});

export const { createTask, deleteTask } = tasksSlice.actions;
export default tasksSlice.reducer;